// material
import { Container, Typography, Grid, Stack, Box } from '@mui/material';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import {
  UserInformation,
  UserProfile,
  OpenOrders,
  AddressDetails,
  AddAddressButton
} from '../../components/_gate/user-fullprofile';

// ----------------------------------------------------------------------

export default function GateFullProfile() {
  const { themeStretch } = useSettings();

  return (
    <Page title="Profile" sx={{ fontFamily: 'inter', mt: -1 }}>
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Stack spacing={3}>
          <Typography variant="subtitle1" sx={{ fontSize: '32px', lineHeight: '48px', fontWeight: '900' }}>
            User Profile
          </Typography>
          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={3}>
            <Stack spacing={5}>
              <UserProfile />
              <OpenOrders />
            </Stack>
            <Stack direction="column" spacing={3} sx={{ flexGrow: 1 }}>
              <UserInformation />

              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={5} alignItems="center">
                <AddressDetails />
                <AddAddressButton />
              </Stack>
            </Stack>
          </Stack>
        </Stack>
      </Container>
    </Page>
  );
}
