// material
import { Container, Typography, Grid, Stack, Avatar, Card, Box } from '@mui/material';
// ----------------------------------------------------------------------
const ORDERS = [
  {
    name: 'Bracelet',
    status: 'Ready for Deilvery',
    orderdate: 'Oct 12th',
    img: '/static/mock-images/products/product_5.jpg'
  },
  {
    name: 'Necklace',
    status: 'Pending order',
    orderdate: 'Jan 12th',
    img: '/static/mock-images/products/product_6.jpg'
  },
  {
    name: 'Helmet',
    status: 'in workshop',
    orderdate: 'Feb 12th',
    img: '/static/mock-images/products/product_7.jpg'
  }
];

export default function OpenOrders() {
  return (
    <Stack spacing={3}>
      <Typography variant="subtitle2" sx={{ fontWeight: '700' }}>
        Open orders
      </Typography>
      {ORDERS.map((opt) => (
        <Card sx={{ p: 2, mb: 2 }} key={opt.name}>
          {/* <Stack direction="row">
            <Avatar alt="My Avatar" src={opt.img} style={{ width: '32px', height: '32px' }} />
            <Stack>
              <Typography variant="subtitle2">{opt.name}</Typography>
              <Typography sx={{ fontSize: '10px', color: '#0C53B7' }}>{opt.status}</Typography>
            </Stack>
            <Typography sx={{ fontSize: '10px' }}>{opt.orderdate}</Typography>
          </Stack> */}
          <Stack direction="row" sx={{ justifyContent: 'space-between' }}>
            <Stack direction="row" sx={{ display: 'flex', alignItems: 'center' }}>
              <Avatar alt={opt.name} src={opt.img} style={{ width: '26px', height: '26px' }} />
              <Box sx={{ ml: 2 }}>
                <Typography variant="subtitle2"> {opt.name}</Typography>
                <Typography variant="body2" sx={{ color: 'text.secondary', fontSize: '10px' }}>
                  {opt.status}
                </Typography>
              </Box>
            </Stack>
            <Typography sx={{ color: 'text.secondary', fontSize: '10px' }}>{opt.orderdate}</Typography>
          </Stack>
        </Card>
      ))}
    </Stack>
  );
}
