import { Icon } from '@iconify/react';
// material
import { useTheme, styled } from '@mui/material/styles';
import { Card, CardHeader, Stack, Avatar, AvatarGroup, Badge, Typography, Box } from '@mui/material';
import Divider from '@mui/material/Divider';
// ----------------------------------------------------------------------
const StyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    backgroundColor: '#44b700',
    color: '#44b700'
  }
}));

const mstyle = {
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center'
};

export default function UserProfile() {
  const theme = useTheme();

  return (
    <>
      <Card sx={{ pb: 3 }}>
        <Box sx={{ backgroundColor: '#6FCF97', height: '12px' }}></Box>
        <Stack spacing={1} sx={{ p: 2 }}>
          <Stack direction="row" justifyContent="center">
            <Avatar
              alt="My Avatar"
              src="/static/mock-images/avatars/avatar_14.jpg"
              sx={{ width: '109px', height: '109px' }}
            />
          </Stack>

          <Stack>
            <Typography variant="subtite1" style={mstyle} sx={{ color: '#00000060', fontWeight: '900', mt: 2 }}>
              Bhanu Jasti
            </Typography>
            <Typography sx={{ fontSize: '12px' }} style={mstyle}>
              Customer
            </Typography>
          </Stack>

          <Stack direction="row" justifyContent="center" spacing={2.5} sx={{ px: 4 }}>
            <Icon
              icon="mdi:calendar-check"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#FFE7D9',
                fontSize: '30px',
                color: '#B72136'
              }}
            />
            <Icon
              icon="mdi:phone"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#FFF7CD',
                fontSize: '30px',
                color: '#FFC107'
              }}
            />
            <Icon
              icon="mdi:email"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#C8FACD',
                fontSize: '30px',
                color: '#00AB55'
              }}
            />
            <Icon
              icon="radix-icons:pencil-2"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#F4F6F8',
                fontSize: '30px',
                color: '#454F5B'
              }}
            />
          </Stack>

          <Divider variant="middle" />

          <Stack direction="row" justifyContent="center" spacing={1}>
            <Icon
              icon="mdi:calendar-check"
              style={{
                fontSize: '28px',
                color: '#916A00'
              }}
            />
            <Stack>
              <Typography sx={{ fontSize: '10px' }}>Appointment</Typography>
              <Typography sx={{ fontSize: '10px' }}>04:00 PM on Aug 5, 2021</Typography>
            </Stack>
          </Stack>

          <Divider />

          <Stack direction="row" style={mstyle} spacing={1}>
            <Typography sx={{ fontSize: '10px' }}>Preffered Hosts</Typography>
            <Stack direction="row">
              <Avatar
                alt="My Avatar"
                src="/static/mock-images/avatars/avatar_15.jpg"
                sx={{ width: '32px', height: '32px' }}
              />
              <Avatar
                alt="My Avatar"
                src="/static/mock-images/avatars/avatar_16.jpg"
                sx={{ width: '32px', height: '32px' }}
              />
            </Stack>
          </Stack>

          <Divider />

          <Stack direction="row" style={mstyle} spacing={2}>
            <Typography sx={{ fontSize: '10px' }}>Refferals</Typography>
            <AvatarGroup max={4}>
              <Avatar
                alt="My Avatar"
                src="/static/mock-images/avatars/avatar_3.jpg"
                style={{ width: '32px', height: '32px' }}
              />
              <Avatar
                alt="My Avatar"
                src="/static/mock-images/avatars/avatar_6.jpg"
                style={{ width: '32px', height: '32px' }}
              />
              <Avatar
                alt="My Avatar"
                src="/static/mock-images/avatars/avatar_5.jpg"
                style={{ width: '32px', height: '32px' }}
              />
            </AvatarGroup>
          </Stack>
        </Stack>
      </Card>
    </>
  );
}
